
const $ = require('jquery')

import floatNum from './tools/float'
import storage from './tools/storage'
import timed from './tools/timed'

export default {
  //
  inList(n, List) {
    const res = $.inArray(n, List)
    if (res < 0) { return false }
    return true
  },
  //
  indexList(n, List) {
    return $.inArray(n, List)
  },
  //
  showModal(id) {
    $('#' + id).modal('show')
  },
  closeModal(id) {
    $('#' + id).modal('hide')
  },
  showToast(msg) {
    $('#dialog').find('div').html(msg)

    $('#dialog').removeClass('d-none')
    setTimeout(function() {
      $('#dialog').addClass('d-none')
    }, 3200)
  },
  // 
  focus(id) {
    $('#' + id).focus()
  },
  //
  go(url) {
    location.href = url
  },
  //
  floatNum,
  // 
  storage,
  //
  timed
} 


