import { net } from '@/service/net.js'
import config from '@/common/config'
import conn_util from '@/common/es/conn_util'

import axios from 'axios'

let url = config.baseURL + config.apiURL + '/member/'
const _url = url 

export default {
  url,
  getList_Member(condition, status) {
    url = _url + conn_util.buildURL(condition, status)

    return net({
      url,
      method: 'GET'
    })
  },
  trash(id) {
    url = _url + id + '/'

    return net({
      url,
      method: 'PUT',
      data: {
        status: 'False'
      }
    })
  },
  add(data) {
    url = _url
    
    return net({
      url,
      method: 'POST',
      data
    })
  },
  edit(id, data) {

    url = _url + id + '/'
    data.status = 'True'

    return net({
      url,
      method: 'PUT',
      data
    })
  },
  getOne(id) {
    url = _url + id + '/'

    return net({
      url,
      method: 'GET'
    })
  }
}


