import { net } from '@/service/net.js'
import config from '@/common/config'
import conn_util from '@/common/es/conn_util'

let url = config.baseURL + config.apiURL + '/price_collect/'
const _url = url 

export default {
  url,
  getList_Prices(condition, status) {

    url = _url + conn_util.buildURL(condition, status)
    return net({
      url,
      method: 'get'
    })
  },
  trash(id) {
    url = _url + id + '/'

    return net({
      url,
      method: 'PUT',
      data: {
        status: 'False'
      }
    })
  },
  add(data) {
    url = _url
    
    return net({
      url,
      method: 'POST',
      data
    })
  },
}


