import { net } from '@/service/net.js'
import config from '@/common/config'

import conn_util from '@/common/es/conn_util'

let url = config.baseURL + config.apiURL + '/freight/'
const _url = url 

export default {
  url,
  getList_Freight(condition, status) {

    url = _url + conn_util.buildURL(condition, status)
    return net({
      url,
      method: 'get'
    })
  }
}


