module.exports = {
  // 空
  val_empty(v, txt) {
    if ((v == null) || (v.trim() == '')) {
      return txt + '不应为空。'
    }
    return true
  },
  val_char(v, txt) {
    let char = /[`~!@#$%^&*(“”‘’'、～·！—_+|=;？，。\\)<>?:"{},.\/;'[\]]/;
    if (char.test(v)) {
      return txt + '中不應有特殊字符。'
    }
    return true
  },
  val_char_price(v, txt) {
    let char = /[`~!@#$%^&*(“”‘’'、～·！—_+|=;？，。\\)<>?:"{},\/;'[\]]/;
    if (char.test(v)) {
      return txt + '中不應有除了.之外的特殊字符。'
    }
    return true
  },
  val_email(v, txt) {
    let char = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
    if (!char.test(v)) {
      return txt + '格式错误。'
    }
    return true
  },
  val_length(v, txt, min, max) {
    if (v.length < min) {
      return txt + '的字符长度应该大于' + min
    }
    if (v.length > max) {
      return txt + '的字符长度应该小于' + max
    }
    return true
  }
}


